import React, { Fragment, useEffect, useState } from "react";
import { toast } from "react-toastify";
import { useDispatch, useSelector } from "react-redux";
import { newBooking, clearErrors } from "../../actions/bookingActions";
import { NEW_BOOKING_RESET } from '../../constants/bookingConstants';

import { getAdminDiscussionContents, getAdminDiscussionTimes} from "../../actions/bookingActions";

// Validation phone function
const validatePhone = (phone) => {
  // Regex cho s in thoi Vit Nam (bt u bng 0 hoc +84)
  const phoneRegex = /^(0|\+84)(\d{9,10})$/;
  return phoneRegex.test(phone);
};

const getCurrentDate = () => {
  const today = new Date();
  const year = today.getFullYear();
  const month = String(today.getMonth() + 1).padStart(2, "0");
  const day = String(today.getDate()).padStart(2, "0");
  return `${year}-${month}-${day}`;
};

const formatDate = (date) => {
  const d = new Date(date);
  const weekdays = [
    "Ch Nht",
    "Th Hai",
    "Th Ba",
    "Th T",
    "Th Nm",
    "Th Su",
    "Th By",
  ];
  const day = String(d.getDate()).padStart(2, "0");
  const month = String(d.getMonth() + 1).padStart(2, "0");
  const year = d.getFullYear();
  const weekday = weekdays[d.getDay()];

  return `${weekday}, ngy ${day} thng ${month} nm ${year}`;
};


const Booking = () => {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.auth);
  const { error, success } = useSelector((state) => state.newBooking);
  const { discussioncontents } = useSelector((state) => state.discussioncontents);
  const { discussiontimes } = useSelector((state) => state.discussiontimes);

  // Fetch data t API thay v hardcode
  const [discussContent, setDiscussContent] = useState([]);
  const [discussTimes, setDiscussTimes] = useState([]);
  const [dataLoading, setDataLoading] = useState(true);

  // State qun l form
  const [formData, setFormData] = useState({
    selectedContent: "",
    otherContent: "",
    selectedTimes: "",
    otherTime: "",
    email: user? user.email : "",
    phone: user? user.phoneNumber: "",
    selectedDate: "",
  });

  const [loading, setLoading] = useState(false);
  const [showConfirmation, setShowConfirmation] = useState(false);

  // Fetch d liu la chn t API khi component mount
  useEffect(() => {
    dispatch(getAdminDiscussionContents());
    dispatch(getAdminDiscussionTimes());
    setDataLoading(false);
  }, []);

  const filtArr = (data, type)=>{
    let result = [];
    if(type === 1){
      for (let index = 0; index < data.length; index++) {
        const element = data[index];
        if(element.trangThai === "Hin"){
          result.push(element.noiDungThaoLuan); 
        }               
      }
    }else if (type === 2){
      for (let index = 0; index < data.length; index++) {
        const element = data[index];
        if(element.trangThai === "Hin"){
          result.push(element.thoiGianThaoLuan);  
        }                 
      }
    }
    return result;    
  }

   // Reset form
   const resetForm = () => {
    setFormData({
      selectedContent: "",
      otherContent: "",
      selectedTimes: "",
      otherTime: "",
      email: user? user.email : "",
      phone: user? user.phoneNumber : "",
      selectedDate: "",
    });
    setShowConfirmation(false);
  };

  useEffect(() => { 
    if(discussioncontents && discussioncontents.length > 0){
      setDiscussContent(filtArr(discussioncontents, 1));
      setDataLoading(false);
    }  

    if(discussiontimes && discussiontimes.length > 0){
      setDiscussTimes(filtArr(discussiontimes, 2));
      setDataLoading(false);
    }   
    
  }, [discussioncontents, discussiontimes]);



  useEffect(() => {
      if (error) {
        dispatch(clearErrors());
      }
  
      if (success) {
        setShowConfirmation(true);
        toast.success("t lch thnh cng!");
        resetForm();
        dispatch({ type: NEW_BOOKING_RESET });

      }
    }, [error, success]);

  // X l checkbox ni dung
  const handleContentChange = (content) => {
    setFormData((prev) => ({
      ...prev,
      selectedContent: prev.selectedContent.includes(content)
        ? prev.selectedContent.filter((item) => item !== content)
        : [...prev.selectedContent, content],
    }));
  };

  // X l checkbox thi gian
  const handleTimeChange = (time) => {
    setFormData((prev) => ({
      ...prev,
      selectedTimes: prev.selectedTimes.includes(time)
        ? prev.selectedTimes.filter((item) => item !== time)
        : [...prev.selectedTimes, time],
    }));
  };

  // X l thay i input
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

 

  // X l submit form
  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validate form
    if (formData.selectedContent.length === 0 && !formData.otherContent) {
      toast.error("Vui lng chn ni dung tho lun");
      return;
    }else{
      formData.selectedContent = JSON.stringify(formData.selectedContent);
    }


    if (formData.selectedTimes.length === 0 && !formData.otherTime) {
      toast.error("Vui lng chn thi gian tho lun");
      return;
    }else{
      formData.selectedTimes = JSON.stringify(formData.selectedTimes);
    }

    if ( !(user && user.email) && !formData.email) {
      toast.error("Vui lng nhp email lin h");
      return;
    }
    if (!(user && user.phoneNumber) && !formData.phone) {
      toast.error("Vui lng nhp s in thoi lin h");
      return;
    }

    if(user){
      if (!validatePhone(user.phoneNumber)) {
        toast.error("S in thoi khng hp l");
        return;
      }
    }else{
      if (!validatePhone(formData.phone)) {
        toast.error("S in thoi khng hp l");
        return;
      }
    }    

    try {
      setLoading(true);      
      // To d liu gi i
      const bookingData = {
        ...formData,
        email: user ? user.email : formData.email,
        phone: user ? user.phoneNumber : formData.phone,
        trangThai: "pending"
      };

      dispatch(newBooking(bookingData));
    
    } catch (error) {
      const errorMessage =
        error.response?.data?.message || "C li xy ra, vui lng th li!";
      toast.error(errorMessage);
      console.error("Booking error:", error);
    } finally {
      setLoading(false);
    }
  };

  // Component xc nhn t lch thnh cng
  const ConfirmationMessage = () => (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white p-6 rounded-lg max-w-md w-full mx-4">
        <h3 className="text-xl font-bold mb-4 text-green-600">
          t lch hn thnh cng!
        </h3>
        <p className="mb-4">
          Cm n bn  t lch hn. Chng ti s lin h vi bn qua email
          hoc s in thoi trong vng 48 gi.
        </p>
        <button
          onClick={() => setShowConfirmation(false)}
          className="bg-green-600 text-white py-2 px-4 rounded-lg font-semibold hover:bg-green-700 transition duration-300 ease-in-out w-full"
        >
          ng
        </button>
      </div>
    </div>
  );

  return (
    <>
      {showConfirmation && <ConfirmationMessage />}

      <form
        onSubmit={handleSubmit}
        className="bg-[#eee9e9] p-6 rounded-lg shadow-md w-full max-w-md mx-auto"
      >
        {/* Tiu  */}
        <h2 className="text-2xl font-bold text-center mb-6">T LCH HN</h2>

        {dataLoading ? (
          <div className="text-center py-4">
            <p>ang ti d liu...</p>
          </div>
        ) : (
          <>
            {/* Ni dung cn tho lun */}
            <div className="mb-6">
              <h3 className="text-lg font-semibold mb-4">
                NI DUNG CN THO LUN
              </h3>
              <div className="space-y-2">
                {discussContent && discussContent.length > 0 && discussContent.map((item, index) => (
                  <label key={index} className="flex items-center">
                    <input
                      type="checkbox"
                      checked={formData.selectedContent.includes(item)}
                      onChange={() => handleContentChange(item)}
                      className="w-5 h-5 mr-2"
                    />
                    <span>{item}</span>
                  </label>
                ))}
                <label className="flex items-center">
                  <span className="mr-2">Khc</span>
                  <input
                    type="text"
                    name="otherContent"
                    value={formData.otherContent}
                    onChange={handleInputChange}
                    className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                    placeholder="Ni dung tho lun khc"
                  />
                </label>
              </div>
            </div>

            {/* Chn ngy */}
            <div className="mb-6">
              <h3 className="text-lg font-semibold mb-2">CHN NGY</h3>
              <div className="space-y-2">
                <input
                  type="date"
                  name="selectedDate"
                  value={formData.selectedDate}
                  min={getCurrentDate()}
                  onChange={handleInputChange}
                  className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                  required
                />
                {formData.selectedDate && (
                  <p className="text-sm text-gray-600 mt-2">
                    Ngy  chn: {formatDate(formData.selectedDate)}
                  </p>
                )}
              </div>
            </div>

            {/* Thi gian tho lun */}
            <div className="mb-6">
              <h3 className="text-lg font-semibold mb-4">
                THI GIAN THO LUN
              </h3>
              <div className="space-y-2">
                {discussTimes && discussTimes.length > 0 && discussTimes.map((item, index) => (
                  <label key={index} className="flex items-center">
                    <input
                      type="checkbox"
                      checked={formData.selectedTimes.includes(item)}
                      onChange={() => handleTimeChange(item)}
                      className="w-5 h-5 mr-2"
                    />
                    <span>{item}</span>
                  </label>
                ))}
                <label className="flex items-center">
                  <span className="mr-2">Khc</span>
                  <input
                    type="text"
                    name="otherTime"
                    value={formData.otherTime}
                    onChange={handleInputChange}
                    className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                    placeholder="Thi gian tho lun khc"
                  />
                </label>
              </div>
            </div>

            {/* Email */}
            <div className="mb-6">
              <label className="block mb-2 text-sm font-medium text-gray-900">
                Email lin h
              </label>
              <input
                type="email"
                name="email"
                value={user? user.email : formData.email}
                disabled = {user? true: false}
                onChange={handleInputChange}
                className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                placeholder="example@gmail.com"
                required
              />
            </div>

            {/* Phone */}
            <div className="mb-6">
              <label className="block mb-2 text-sm font-medium text-gray-900">
                S in thoi lin h
              </label>
              <input
                type="tel"
                name="phone"
                value={user?user.phoneNumber: formData.phone}
                disabled = {user? true: false}
                onChange={handleInputChange}
                className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                placeholder="0xxxxxxxxx"
                required
                pattern="(0|\+84)[0-9]{9,10}"
                title="Nhp s in thoi Vit Nam hp l (bt u bng 0 hoc +84)"
              />
              <p className="text-xs text-gray-500 mt-1">
                VD: 0989123456 hoc +84989123456
              </p>
            </div>

            {/* Thng tin phn hi */}
            <div className="mb-6">
              <p className="text-sm text-justify text-gray-600 mb-2">
                H thng s phn hi qua email hoc tin nhn n Anh Ch chm
                nht trong vng 48 gi.
              </p>
              <p className="text-sm text-justify text-gray-600">
                Nu Anh Ch phi ch qu lu, vui lng gi n <br /> 0989 165
                465  t lch hn nhanh!
              </p>
            </div>

            <button
              type="submit"
              disabled={loading}
              className={`py-2 px-4 rounded-lg font-semibold transition duration-300 ease-in-out w-full ${
                loading
                  ? "bg-gray-400 text-white cursor-not-allowed"
                  : "bg-black text-yellow-400 hover:bg-gray-900 hover:text-white"
              }`}
            >
              {loading ? "ang x l..." : "t lch hn"}
            </button>
          </>
        )}
      </form>
    </>
  );
};

export default Booking;
